
/* General Container Style */
.container {
  width: 100%;
  padding: 15px;
  margin: 0 auto;
  max-width: 100%;
  box-sizing: border-box;
  transition: background-color 0.9s ease, color 0.3s ease;

}

textarea {
  width: 100%;
  height: 20vw;
  padding: 10px;
  font-size: 1rem;
  outline: none;
  box-sizing: border-box;
  transition: background-color 0.9s ease, color 0.3s ease;

}

button {
  font-size: 14px;
  padding: 10px;
  width: 100%; 
  margin-bottom: 10px;
}

@media (min-width: 320px) and (max-width: 760px) {
  .custom-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  .custom-grid .col {
    padding: 5px;
  }
}
